import { createRouter, createWebHistory } from 'vue-router'
import Call from '../views/Call.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/call/',
    name: 'Call',
    component: Call
  },
  // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º —Å—Ç–∞—Ä—ã–µ —Å—Å—ã–ª–∫–∏ —Å UUID –≤ –ø—É—Ç–∏
  {
    path: '/call/:uuid',
    name: 'LegacyCall',
    component: Call,
    beforeEnter: (to, from, next) => {
      // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –Ω–æ–≤—ã–π —Ñ–æ—Ä–º–∞—Ç —Å query –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º
      next({ path: '/call/', query: { uuid: to.params.uuid } })
    }
  },
  // Fallback –¥–ª—è –≤—Å–µ—Ö –æ—Å—Ç–∞–ª—å–Ω—ã—Ö –º–∞—Ä—à—Ä—É—Ç–æ–≤
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.NODE_ENV === 'production' ? '/nofelet-ui/' : '/'),
  routes
})

export default router